
body {
  margin: 0;
  padding: 0;
}
#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #222;
}
.container {
  margin: 0 auto;
  max-width: 1024px;
  width: 100%;
}
input[type=text],
.stripe-input,
.content-container {
  border-radius: 2px;
}
.content-container {
  background-color: rgba(255, 255, 255, 0.65);
  -webkit-box-shadow: inset 0 0px 50px rgba(255, 255, 255, 0.6);
          box-shadow: inset 0 0px 50px rgba(255, 255, 255, 0.6);
  border-top: 1px solid rgba(255, 255, 255, 0.8);
}
input[type=text],
.stripe-input {
  background-color: rgba(255, 255, 255, 0.3);
  -webkit-box-shadow: 0 0px 2px #000;
          box-shadow: 0 0px 2px #000;
  border: none;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
input[type=text] {
  font-size: 0.8rem;
  padding: 0.25rem;
}
.error {
  background-color: #eb1111;
  color: #fff;
  padding: 0.5rem 1rem;
}
button {
  background: #8f2222;
  border: 1px solid #6e0b0b;
  border-radius: 2px;
  -webkit-box-shadow: inset 0 1px 0px #b74e4e;
          box-shadow: inset 0 1px 0px #b74e4e;
  color: #fff;
  cursor: pointer;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
button:hover {
  background: #a93131;
}
hr {
  border-top: 1px solid rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.main {
  background-attachment: fixed;
  background-image: url('/static/img/challenger.png');
  background-position: center;
  background-size: cover;
  position: relative;
}
.logo {
  margin-bottom: 2rem;
  max-width: calc(100% - 4rem);
  width: 500px;
}

.cart {
  margin: 2rem auto 0;
  max-width: calc(100% - 3rem);
  padding: 1rem;
  text-align: left;
  width: 600px;
}
.cart-product {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.total {
  text-align: right;
}
.submitting-container {
  text-align: right;
}
a,
a:hover,
a:focus {
  color: #08d;
  cursor: pointer;
}

.stripe-container {
  margin: 0 auto;
  max-width: 100%;
  width: 500px;
}
.error {
  margin: 1rem 0;
}
.success {
  color: #0f4;
}
.card-element {
  padding: 0.5rem 1rem;
}
.stripe-container h3 {
  margin: 0 0 0.5rem;
  padding: 0;
}

.cart-form {
  margin: 0 auto;
  max-width: 500px;
  /*padding: 0 1rem;*/
  text-align: left;
}
.text-input label {
  display: inline-block;
  text-align: right;
  width: 40%;
}
.text-input,
.text-input .input-container {
  text-align: left;
}
.text-input .input-container {
  width: calc(60% - 0.5rem);
}
.text-input input {
  max-width: 100%;
  width: 100%;
}
.cart-form h3 {
  margin: 0 0 1rem;
  padding: 0;
}
input[type=radio] {
  border: 1px solid #f00;
  margin-left: 25%;
}

.text-input {
  margin-bottom: 0.3rem;
}
.input-container {
  display: inline-block;
}

.edit-modal-container {
  background: rgba(0, 0, 0, 0.5);
  height: 100%;
  position: fixed;
  top: 0;
  width: 100%;
}
.edit-modal {
  background: #fff;
  border-radius: 1rem;
  display: inline-block;
  left: calc(50% - 20rem);
  padding: 1rem;
  position: fixed;
  right: calc(50% - 20rem);
  top: 15%;
}

.footer[data-v-6437ead1] {
  color: rgba(255, 255, 255, 0.5);
  margin-top: 3rem;
  padding: 0.5rem;
}

.nav[data-v-e4a49d62] {
  color: #fff;
}
h3[data-v-e4a49d62] {
  display: inline-block;
}
.container[data-v-e4a49d62] {
  text-align: right;
}

.product-list-container[data-v-692564fe] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
h2[data-v-692564fe] {
  color: #fff;
}

.product[data-v-bfaa7e78] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  margin: 0 0.5rem 1rem;
  max-width: 100%;
  padding: 0.5rem 0.5rem 1.5rem;
  text-align: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  width: 300px;
}
